import React, { Component } from 'react';
import './App.css';
import OpenTrivia from 'opentrivia-react'

class App extends Component {
  render() {
    return (
      <div className="App">
        <OpenTrivia amount={45} getToken={true} />
      </div>
    );
  }
}

export default App;



